//@import "/node_modules/bootstrap-sass/assets/stylesheets/bootstrap";;

html, body {
  height: 100%;
  width: 100%;
  background-color: white;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato';
}

@mixin def-clr {
  background-color: white;
}

.content-holder, #line-container {
  @include def-clr;
}

.container {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  width: 85vw;
  vertical-align: top;
}

.content {
  text-align: center;
  display: inline-block;
  margin: 5px;
}

.title {
  font-weight: 100;
  font-size: 96px;
  margin-bottom: 20px;
}

.md-block {
  padding: 10px;
  width: auto;
}

.md-sidenav-left {
  text-align: left;
  min-height: 100%;
  position: fixed;
}

#avatar-item {
  min-height: 200px;
}

#avatar-image {
  height: 100px;
  width: 100px;
}

#options-menu {
  margin-left: 20px;
}

.content-holder, #line-container {
  margin-top: 25px;
}

md-list .md-button {
  width: 100%;
}

md-backdrop {
  position: fixed;
}

#login-container {
  margin-top: 100px;
}

md-card {
  display: inline-block;
}

.post-card {
  height: 300px;
  width: 350px;
}

.md-button.md-icon-button.menuBtn {
  margin-top: 6px;
  margin-right: 0;
}

.user-list {
  min-width: 250px;
  max-height:85%;
  height: 85%;
  overflow: hidden;
}

#user-list-container{
  min-width: 200px;
}

.user-list .md-button {
  color: inherit;
  text-align: left;
  width: 100%;
}

.user-list .selected {
  color: #03a9f4;
}

.face {
  border-radius: 30px;
  border: 1px solid #ddd;
  display: inline-block;
  margin: 4px 16px;
  vertical-align: middle;
  width: 48px;
}

.content-wrapper {
  position: relative;
}

.usersandposts {
  height: 85vh;
  max-height: 85vh;
  overflow-y: auto;
  overflow-x: hidden;
}

.middle-align{
  width: 99%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 2%;
}

#primary-col {
  overflow: hidden;
  height: 85%;
  max-height:85%;
}

#primary-col .md-button {
  margin: 8px auto 16px 0;
}

#primary-col .cell {
  flex: 1 1 auto;
}

#primary-col img {
  display: block;
  max-width: 100%;
}

#map {
  height: 500px;
  width: 100%;
}